/**
 * ‰ΩøÁî®ËÖæËÆØÂú∞ÂõæAPIËøõË°åÂú∞ÁÇπÊêúÁ¥¢
 */
const searchLocation = (keyword) => {
    return new Promise((resolve, reject) => {
        uni.request({
            url: `https://apis.map.qq.com/ws/place/v1/search`,
            method: 'GET',
            data: {
                key: TENCENT_MAP_KEY,
                keyword: keyword,
                boundary: 'nearby(' + latitude.value + ',' + longitude.value + ',10000)', // ÊêúÁ¥¢ËåÉÂõ¥10km
                page_size: 10,
                page_index: 1
            },
            success: (res) => {
                if (res.data && res.data.status === 0 && res.data.data.length > 0) {
                    resolve(res.data.data);
                } else {
                    reject(new Error('Êú™ÊâæÂà∞Áõ∏ÂÖ≥Âú∞ÁÇπ'));
                }
            },
            fail: (err) => {
                reject(err);
            }
        });
    });
};

/**
 * Â§ÑÁêÜÊêúÁ¥¢
 */
const handleSearch = async () => {
    if (!searchText.value.trim()) {
        uni.showToast({
            title: 'ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÜÖÂÆπ',
            icon: 'none'
        });
        return;
    }
    
    isLoading.value = true;
    loadingText.value = 'ÊêúÁ¥¢‰∏≠...';
    
    // Ê∑ªÂä†Ëß¶ËßâÂèçÈ¶à
    try {
        uni.vibrateShort({
            type: 'light'
        });
    } catch (error) {
        console.warn('Ëß¶ËßâÂèçÈ¶à‰∏çÊîØÊåÅ:', error);
    }
    
    try {
        // È¶ñÂÖàÂú®Êú¨Âú∞Êï∞ÊçÆ‰∏≠ÊêúÁ¥¢
        const localResults = birdLocations.value.filter(location => 
            location.name.includes(searchText.value) || 
            location.category.includes(searchText.value) ||
            location.description.includes(searchText.value) ||
            location.commonBirds.some(bird => bird.name.includes(searchText.value))
        );
        
        if (localResults.length > 0) {
            // Â¶ÇÊûúÊâæÂà∞Êú¨Âú∞ÁªìÊûúÔºåÁßªÂä®Âà∞Á¨¨‰∏Ä‰∏™ÁªìÊûú
            const firstResult = localResults[0];
            latitude.value = firstResult.latitude;
            longitude.value = firstResult.longitude;
            scale.value = 16;
            
            // È´ò‰∫ÆÊêúÁ¥¢ÁªìÊûú
            highlightSearchResults(localResults);
            
            uni.showToast({
                title: `ÊâæÂà∞${localResults.length}‰∏™Áõ∏ÂÖ≥Âú∞ÁÇπ`,
                icon: 'success'
            });
        } else {
            // Â¶ÇÊûúÊú¨Âú∞Ê≤°ÊúâÁªìÊûúÔºå‰ΩøÁî®ËÖæËÆØÂú∞ÂõæAPIÊêúÁ¥¢
            const result = await searchLocation(searchText.value);
            if (result && result.length > 0) {
                // Êõ¥Êñ∞Âú∞Âõæ‰∏≠ÂøÉÁÇπ
                latitude.value = result[0].location.lat;
                longitude.value = result[0].location.lng;
                
                // Ê∑ªÂä†ÊêúÁ¥¢ÁªìÊûúÊ†áËÆ∞
                const searchMarker = {
                    id: 999,
                    latitude: result[0].location.lat,
                    longitude: result[0].location.lng,
                    title: result[0].title,
                    iconPath: getOSSUrl('static/icons/marker-search.png', 'marker'),
                    width: 40,
                    height: 40,
                    callout: {
                        content: result[0].title,
                        color: '#000000',
                        fontSize: 12,
                        borderRadius: 5,
                        bgColor: '#ffffff',
                        padding: 5,
                        display: 'ALWAYS'
                    }
                };
                
                // ‰øùÁïôÊï∞ÊçÆÂ∫ìÂú∞ÁÇπÊ†áËÆ∞Âπ∂Ê∑ªÂä†ÊêúÁ¥¢ÁªìÊûúÊ†áËÆ∞
                const dbMarkers = markers.value.filter(marker => marker.id !== 999);
                markers.value = [...dbMarkers, searchMarker];
                
                scale.value = 16;
                
                uni.showToast({
                    title: 'ÊêúÁ¥¢ÊàêÂäü',
                    icon: 'success'
                });
            }
        }
    } catch (error) {
        console.error('ÊêúÁ¥¢Â§±Ë¥•:', error);
        uni.showToast({
            title: 'ÊêúÁ¥¢Â§±Ë¥•: ' + error.message,
            icon: 'none'
        });
    } finally {
        isLoading.value = false;
    }
};

/**
 * È´ò‰∫ÆÊêúÁ¥¢ÁªìÊûú
 */
const highlightSearchResults = (results) => {
    // Êõ¥Êñ∞Ê†áËÆ∞ÔºåÈ´ò‰∫ÆÊêúÁ¥¢ÁªìÊûú
    markers.value = birdLocations.value.map(location => {
        const isHighlighted = results.some(result => result.id === location.id);
        return {
            id: location.id,
            latitude: location.latitude,
            longitude: location.longitude,
            title: location.name,
            iconPath: getOSSUrl(
                isHighlighted ? 'static/icons/marker-highlighted.png' : location.markerIcon, 
                'marker'
            ),
            width: isHighlighted ? 50 : 40,
            height: isHighlighted ? 50 : 40,
            callout: {
                content: location.name,
                color: isHighlighted ? '#ff6b6b' : '#000000',
                fontSize: isHighlighted ? 14 : 12,
                borderRadius: 5,
                bgColor: isHighlighted ? '#fff5f5' : '#ffffff',
                padding: 5,
                display: 'ALWAYS'
            }
        };
    });
};

