/**
 * Âä†ËΩΩÈ∏üÁ±ªËßÇÊµãÁÇπÊï∞ÊçÆ
 */
const loadBirdLocations = () => {
    isLoading.value = true;
    loadingText.value = 'Âä†ËΩΩËßÇÊµãÁÇπÊï∞ÊçÆ...';
    
    try {
        // Ê®°ÊãüAPIÂª∂Ëøü
        setTimeout(() => {
            const locationMarkers = birdLocations.value.map(loc => ({
                id: loc.id,
                latitude: loc.latitude,
                longitude: loc.longitude,
                title: loc.name,
                iconPath: getOSSUrl(loc.markerIcon, 'marker'),
                width: 40,
                height: 40,
                callout: {
                    content: loc.name,
                    color: '#000000',
                    fontSize: 12,
                    borderRadius: 5,
                    bgColor: '#ffffff',
                    padding: 5,
                    display: 'ALWAYS'
                }
            }));
            
            // Êõ¥Êñ∞Ê†áËÆ∞
            markers.value = locationMarkers;
            
            // ÁªòÂà∂Âå∫ÂüüËæπÁïå
            drawAreaBoundary(birdLocations.value);
            
            isLoading.value = false;
            
            uni.showToast({
                title: `Â∑≤Âä†ËΩΩ${birdLocations.value.length}‰∏™ËßÇÊµãÁÇπ`,
                icon: 'success',
                duration: 1500
            });
        }, 800);
    } catch (error) {
        console.error('Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•:', error);
        isLoading.value = false;
        uni.showToast({
            title: 'Âä†ËΩΩÊï∞ÊçÆÂ§±Ë¥•',
            icon: 'none'
        });
    }
};

/**
 * ÁªòÂà∂Âå∫ÂüüËæπÁïå
 */
const drawAreaBoundary = (locations) => {
    if (locations.length < 3) return; // Ëá≥Â∞ëÈúÄË¶Å3‰∏™ÁÇπÊâçËÉΩÂΩ¢Êàê‰∏Ä‰∏™Âå∫Âüü
    
    // ‰ΩøÁî®ÊâÄÊúâÁÇπÊûÑÂª∫‰∏Ä‰∏™Â§öËæπÂΩ¢
    const points = locations.map(loc => ({
        latitude: loc.latitude,
        longitude: loc.longitude
    }));
    
    // Ê∑ªÂä†Á¨¨‰∏Ä‰∏™ÁÇπ‰Ωú‰∏∫ÁªìÊùüÁÇπÔºåÂΩ¢ÊàêÈó≠ÁéØ
    points.push(points[0]);
    
    polyline.value = [{
        points: points,
        color: '#1E90FF77', // ÂçäÈÄèÊòéËìùËâ≤
        width: 2,
        dottedLine: false,
        arrowLine: false
    }];
};

